import React from "react";
import Table from "react-bootstrap/Table";
import CustomSelect from "../Select/customSelect";
import "./staticTable.css";

const StaticTable = ({ preparedData, handleSelectChange }) => {
  return (
    <Table bordered className="table-light">
      <thead>
        <tr>
          <th className="header-styling">Column Name</th>
          <th className="header-styling">Attribute Name</th>
        </tr>
      </thead>
      <tbody>
        {preparedData.map((item, index) => {
          const {
            header,
            isMandatory,
            hasNLS,
            currentMapping,
            dropdownOptions,
          } = item;

          return (
            <tr key={index}>
              <td>{header}</td>
              <td>
                <CustomSelect
                  index={index}
                  selectedValue={currentMapping}
                  onChange={(index, value) => {
                    handleSelectChange(header, value);
                  }}
                  size="lg"
                  className="w-100"
                  disabled={!hasNLS}
                  options={{
                    defaultLabel: hasNLS
                      ? header
                      : "Please select from Drop Down",
                    list: dropdownOptions,
                  }}
                />
              </td>
            </tr>
          );
        })}
      </tbody>
    </Table>
  );
};

export default StaticTable;
